'use strict';

var Slider = require('./../pages/slider');

var Home = function () {
    'use strict';

    var load = function load() {
        Slider.init();
        var hash = window.location.hash.substring(1);
        if (hash === 'signup') {
            setTimeout(function () {
                $.scrollTo($('#verify-email-form'), 500);
            }, 500);
        }
    };

    var unload = function unload() {
        Slider.destroy();
    };

    return {
        load: load,
        unload: unload
    };
}();

module.exports = Home;


